@import "../../../bower_components/bootstrap/less/variables.less";
@import "../../../bower_components/bootstrap/less/mixins.less";
@import "../../../bower_components/bootstrap/less/wells.less";
@import "../../../bower_components/fontawesome/less/font-awesome.less";

@import "map.less";
@import "navigator.less";
@import "overview.less";


// Variables
@dis-color: hsla(198, 73%, 63%, 1);
@dai-text-shadow-dark: 3px 3px 4px hsla(80, 50%, 5%, 1);

@agent-font-size-xs: 1.2em;
@agent-font-size-sm: 1.6em;
@agent-font-size: 2.1em;
@agent-font-size-lg: 3.1em;
@agent-font-size-xl: 3.7em;

//ngAnimate ng-animate
.animate-show.ng-hide-add,
.animate-show.ng-hide-remove {
  transition: all .6s linear 0.1s;
}

.animate-show.ng-hide {
  opacity: 0;
}


// Mixins
.movable(@zid: 50, @col: #111111) {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  z-index: @zid;
  background-color: @col;
  cursor: grab;
  border-radius: 15px;
  border: 2px solid darken( @col, 20% );
}

.dai-icon( @content, @color, @size ) {
  .fa-icon();
  content: @content;
  color: @color;
  font-size: @size;
  width: (18em / 14);
  text-align: center;
  text-shadow: @dai-text-shadow-dark;
  // border-radius: 5px;
}

// Recursive mixins
.generate-agents(@n, @size, @i: 1) when (@i =< @n) {
  .agent-@{i} {
    color: hsla( 65 + mod(@i * 50, 360), 100%, 60%, 1 );
    text-shadow: 3px 1px 2px black;
  }
  .agent-@{i}:before {
    .dai-icon( @fa-var-user-secret, inherit, @size );
  }
  .generate-agents(@n, @size, (@i + 1));
}
