@import "dai.less";

@base-color-stroll: hsl(150, 100%, 40%);
@base-color-bike: hsl(310, 100%, 40%);
@base-color-kaper: hsl(226, 95%, 40%);
@base-color-amager: hsl(260, 95%, 50%);

.generate-weight( @w, @icon, @base-color ) {
	.w@{w} {
		color: @base-color;
	}
	.w@{w}:before {
		.dai-icon( @icon, inherit, @agent-font-size-sm );
		position: absolute; // Ensures no overflow from icon
		opacity: 0.7;
	}
}

#dai {
	.navigator {
		position: absolute;
		.movable( 50, @dis-color );
		right: 25%;
		top: 6%;
		h4 {
			.generate-agents(12, @agent-font-size-xs);
		}

		.graph {
			border-radius: 5px;
			width: 100%;
			.text-overflow();
			.row {
				width: 100%;
				height: 30px;
				margin: 0px 0px;
				padding: 0px 0px;
				i {
					display: inline-block;
		      min-width: 30px;
		      min-height: 30px;
		      border-radius: 50%;
					border: 1px solid black;
					background-color: #eee;
				}
			}
			.moving-agent {
				position: absolute;
				z-index: 1500;
			}
			.agent-additional {
				position: absolute;
				width: 200px;
				left: -100px;
				text-align: center;
				span {
					color: #eee;
				}
			}
			.generate-agents(12, @agent-font-size);
			.stroll, .bike, .kaper, .amager {
				.w-10000 {
					background-color: darken(@dis-color, 45%);
				}
			}
			.stroll {
				.generate-weight( 15, @fa-var-camera, darken( @base-color-stroll, 5%) );
				.generate-weight( 16, @fa-var-twitter, darken( @base-color-stroll, 5%) );
				.generate-weight( 20, @fa-var-mobile, darken( @base-color-stroll, 5%) );
				.generate-weight( 17, @fa-var-trash, lighten( @base-color-stroll, 4%) );
				.generate-weight( 18, @fa-var-umbrella, lighten( @base-color-stroll, 6%) );
				.generate-weight( 23, @fa-var-gift, lighten( @base-color-stroll, 8%) );
				.generate-weight( 26, @fa-var-envelope, lighten( @base-color-stroll, 10%) );
			}
			.bike {
				.generate-weight( 15, @fa-var-camera, darken( @base-color-bike, 5%) );
				.generate-weight( 16, @fa-var-twitter, darken( @base-color-bike, 5%) );
				.generate-weight( 20, @fa-var-mobile, darken( @base-color-bike, 5%) );
				.generate-weight( -5, @fa-var-wheelchair, @base-color-bike );
				.generate-weight( -10, @fa-var-bus, @base-color-bike );
				.generate-weight( -6, @fa-var-female, @base-color-bike );
			}
			.kaper {
				.generate-weight( 15, @fa-var-camera, darken( @base-color-kaper, 5%) );
				.generate-weight( 16, @fa-var-twitter, darken( @base-color-kaper, 5%) );
				.generate-weight( 20, @fa-var-mobile, darken( @base-color-kaper, 5%) );
				.generate-weight( -20, @fa-var-institution, @base-color-kaper );
				.generate-weight( 9, @fa-var-plane, @base-color-kaper );
				.generate-weight( 22, @fa-var-cloud, @base-color-kaper );
				.generate-weight( 40, @fa-var-life-ring, @base-color-kaper );
			}
			.amager {
				.generate-weight( 15, @fa-var-camera, darken( @base-color-amager, 5%) );
				.generate-weight( 16, @fa-var-twitter, darken( @base-color-amager, 5%) );
				.generate-weight( 20, @fa-var-mobile, darken( @base-color-amager, 5%) );
				.generate-weight( 13, @fa-var-music, @base-color-amager );
				.generate-weight( 12, @fa-var-truck, @base-color-amager );
				.generate-weight( 21, @fa-var-eye, @base-color-amager );
				.generate-weight( 0, @fa-var-bicycle, @base-color-amager );
			}
		}
  }


}

