@import "dai.less";
@activity-font-size: 2.2em;
@location-color: hsla(215, 100%, 90%, 1);
@location-font-size: unit(@activity-font-size*.65, em);

@activity-color-1: hsla( 199, 100%, 58%, 1 );
@activity-color-2: hsla(158, 100%, 33%, 1);
@activity-color-3: hsla( 36, 100%, 58%, 1 );
@activity-color-4: hsla( 360, 100%, 33%, 1 );

.activity-bike:before {
  .dai-icon( @fa-var-bicycle, @activity-color-3, unit(@activity-font-size*1.1, em) );
}
.activity-bodega:before {
  .dai-icon( @fa-var-glass, @activity-color-2, @activity-font-size );
}
.activity-pro:before {
  .dai-icon( @fa-var-futbol-o, @activity-color-3, @activity-font-size );
}
.activity-kaper:before {
  .dai-icon( @fa-var-ship, @activity-color-4, @activity-font-size );
}
.activity-stroll:before {
  .dai-icon( @fa-var-group, @activity-color-1, @activity-font-size );
}
.activity-amager:before {
  .dai-icon( @fa-var-cloud, @activity-color-2, @activity-font-size );
}
.activity-samraad:before {
  .dai-icon( @fa-var-comment, @activity-color-4, @activity-font-size );
}
.activity-eksamen:before {
  .dai-icon( @fa-var-comment, @activity-color-3, @activity-font-size );
}
.activity-location:before {
  .dai-icon( @fa-var-institution, @location-color, @location-font-size );
}

#dai {
	.map {
    position: relative;
    height: 90%;
    width: 100%;
    .angular-google-map-container {
      min-height: 550px;
      min-width: 600px;
      border: 2px solid @dis-color;
  		border-radius: 5px;
      padding: 2px 2px;
    }

    .marker-container {
      position: relative;
      overflow: visible !important;
      display: block;
      width: 230px;
      .marker-row {
        position: relative;
        display: block;
        left: -10px;
        width: 100%;
        text-align: center;
        .clearfix();
      }
      .agents {
        left: -30px;
        top: -15px;
      }
      .location {
        font-size: @location-font-size;
        color: lighten( @location-color, 2%);
        text-shadow: @dai-text-shadow-dark;
      }
      .generate-agents(12, @agent-font-size-lg);
    }
  }
}
