@import "dai.less";

#dai {
	.overview {
		border: 2px solid @dis-color;
		border-radius: 5px;
		padding: 2px 2px;
		background-color: rgb(240, 240, 240);
		div {
			.center-block();
			padding: 0px 0px;
			margin: 0px;
			.agent-avatar {
				position: relative;
				display: inline-block;
				.text-overflow();
			}
			.naming {
				.text-overflow();
				b {
					font-size: 1.2em;
					vertical-align: top;
					max-width: 100%;
				}
			}
			.scoring {
				position: relative;
				span {
					position: absolute;
					color: darken( @dis-color, 12% );
					font-size: 0.95em;
					left: -3px;
					bottom: 0px;
				}
				.fa {
					color: #e3cf7a;
				}
				.score {
					margin-left: 5px;
					font-size: 1.45em;
					font-family: monospace;
					vertical-align: top;
				}
			}
			.action {
				color: darken( @dis-color, 12% );
				font-size: 0.85em;
				font-family: monospace;
				margin-left: 10px;
			}
		};
		.generate-agents(12, @agent-font-size-xl);
	}
}
